<template>
	<view class="account">
		<navbar :type="2" title="账号安全"></navbar>
		<view class="main">
			<view class="main-title">绑定邮箱</view>
			<input class="main-input" type="text" v-model="email" disabled @click="showEmail"/>
		</view>
		<view class="main">
			<view class="main-title">修改密码</view>
			<input class="main-input" type="password" v-model="password" disabled  @click="showPwd"/>
		</view>
		<u-popup v-model="showPop" mode="center" border-radius="14">
			<view class="set-account" v-if="type==1">
				<text class="set-account-title">修改绑定邮箱</text>
				<input class="set-account-input" v-model="newEmail" placeholder="请输入您要绑定的邮箱" />
				<button type="default" class="set-account-button" @click="updateEmail">确认</button>
			</view>
			<view class="set-account set-pwd" v-if="type==2">
				<text class="set-account-title">修改密码</text>
				<input class="set-account-input" v-model="pwd" placeholder="请输入您当前密码" />
				<input class="set-account-input" v-model="newPwd" placeholder="请输入您要修改的密码" />
				<input class="set-account-input" v-model="newPwd" placeholder="请确认您要修改的密码" />
				<button type="default" class="set-account-button" @click="updatePwd">确认</button>
			</view>
		</u-popup>
		<button type="default" class="button" @click="showAction = true">注销登录</button>
		<u-action-sheet :list="list" v-model="showAction" @click="clickAction"></u-action-sheet>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				email: '2364565456@qq.com',
				password: 'abc123456',
				list: [{
					text: '退出登录',
					color: 'rgb(133, 203, 248)'
				}, {
					text: '注销账户',
					color: '#dd524d',
					subText: '注销账户后不可找回，确定注销吗'
				}],
				showAction: false,
				showPop: false,
				type: null,
				newEmail:'',
				pwd:'',
				newPwd:''
			}
		},
		methods: {
			showEmail(){
				this.type = 1;
				this.showPop = true;
			},
			showPwd(){
				this.type = 2;
				this.showPop = true;
			},
			updateEmail(){
				if(!this.newEmail){
					this.showPop = false;
					return this.$chat.alert('请先输入邮箱号','none')
				}else{
					this.email = this.newEmail;
					this.showPop = false;
					return this.$chat.alert('修改邮箱号成功')
				}
			},
			updatePwd(){
				
			},
			clickAction(index){
				if(index===0){
					uni.navigateTo({
						url:'../login/login'
					})
				}else{
					uni.navigateTo({
						url:'../login/login'
					})
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
.main{
	padding: 0 30rpx;
	margin: 50rpx 0;
	.main-title{
		font-size: 18px;
		font-weight: bold;
		color: $theme-color;
	}
	.main-input{
		width: 100%;
		height: 80rpx;
		line-height: 80rpx;
		padding: 0 20rpx;
		box-sizing: border-box;
		border: 1px solid #ccc;
		border-radius: 5px;
		margin-top: 20rpx;
	}
}
.set-account{
	width: 600rpx;
	height: 350rpx;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	.set-account-title{
		color: $theme-color;
		font-size: 16px;
		font-weight: bold;
	}
	.set-account-input{
		width: 450rpx;
		padding: 0 20rpx;
		border-radius: 6px;
		height: 80rpx;
		border: 1px solid #ccc;
		font-size: 14px;
	}
	.set-account-button{
		width: 150rpx;
		height: 70rpx;
		line-height: 70rpx;
		background-color: $theme-color;
		color: #fff;
		font-size: 16px;
		&:after{
			border: none;
		}
	}
}
.set-pwd{
	height: 550rpx;
}
.button{
	width: 500rpx;
	margin: 80rpx auto;
	height: 90rpx;
	line-height: 90rpx;
	background-color: $uni-color-error;
	color: #fff;
	font-weight: bold;
	&:after{
		border: none;
	}
}
</style>
